import React, { useMemo, useState } from 'react';
import {
  Dialog, DialogTitle, DialogContent, DialogActions,
  TextField, Stack, Button
} from '@mui/material';
import { createMaintenance } from '../../api/maintenance';

export default function CreateMaintenanceModal({
  roomNumber,    //  RoomDetail
  roomId,        // fallback 
  open,
  onClose,
  onSuccess
}) {
  const today = useMemo(() => new Date().toISOString().slice(0, 10), []);

  // --- State ---
  const [inputRoomNumber, setInputRoomNumber] = useState(roomNumber ?? roomId ?? '');
  const [scheduledDate, setScheduledDate] = useState(today);
  const [description, setDescription] = useState('');
  const [costBaht, setCostBaht] = useState('');
  const [busy, setBusy] = useState(false);
  const [err, setErr] = useState('');

  //  ( props )
  const effectiveRoomNumber = inputRoomNumber ? Number(inputRoomNumber) : null;

  // --- Handle Submit ---
  async function submit() {
    if (!effectiveRoomNumber) {
      setErr('');
      return;
    }
    if (!description.trim()) {
      setErr('');
      return;
    }
    if (!scheduledDate) {
      setErr('');
      return;
    }

    setBusy(true);
    setErr('');

    try {
      const payload = {
        roomNumber: effectiveRoomNumber,
        description: description.trim(),
        scheduledDate, // YYYY-MM-DD
        costBaht: costBaht === '' ? null : Number(costBaht)
      };

      await createMaintenance(payload);
      onSuccess?.();
      onClose?.();
    } catch (e) {
      const msg =
        (e?.body && (e.body.error || e.body.message)) ||
        e?.message ||
        'Create failed';
      setErr(msg);
    } finally {
      setBusy(false);
    }
  }

  return (
    <Dialog open={open} onClose={busy ? undefined : onClose} maxWidth="xs" fullWidth>
      <DialogTitle>
        
        {roomNumber && `   ${roomNumber}`}
      </DialogTitle>

      <DialogContent dividers>
        <Stack spacing={2} sx={{ mt: 1 }}>
          {/*  roomNumber  props  */}
          {!roomNumber && (
            <TextField
              label=""
              type="number"
              value={inputRoomNumber}
              onChange={(e) => setInputRoomNumber(e.target.value)}
              fullWidth
            />
          )}

          <TextField
            type="date"
            label=""
            InputLabelProps={{ shrink: true }}
            value={scheduledDate}
            onChange={(e) => setScheduledDate(e.target.value)}
            fullWidth
          />

          <TextField
            label=""
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            placeholder=" , ..."
            multiline
            minRows={2}
            fullWidth
          />

          <TextField
            type="number"
            label=" ()"
            value={costBaht}
            onChange={(e) => setCostBaht(e.target.value)}
            inputProps={{ step: '0.01', min: '0' }}
            fullWidth
          />

          {err && <div style={{ color: 'crimson' }}>{err}</div>}
        </Stack>
      </DialogContent>

      <DialogActions>
        <Button onClick={onClose} disabled={busy}></Button>
        <Button
          variant="contained"
          onClick={submit}
          disabled={
            busy ||
            !effectiveRoomNumber ||
            !description.trim()
          }
        >
          
        </Button>
      </DialogActions>
    </Dialog>
  );
}
